import React, { Component } from "react";
import { observer } from "mobx-react";
import Store from "./Store";
import Styles from "./Styles";
import TextField from "material-ui/TextField";
import DropDownMenu from "material-ui/DropDownMenu";
import MenuItem from "material-ui/MenuItem";
import { RadioButton, RadioButtonGroup } from "material-ui/RadioButton";
import Toggle from "material-ui/Toggle";
import Slider from "material-ui/Slider";
import { Container, Row, Col } from "react-grid-system";

@observer
class GeneForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showAlleleFrequencyOptions: true,
      showZygosityOptions: true
    };
  }

  handleToggle = (e, isToggled) => {
    this.setState({
      showAlleleFrequencyOptions: isToggled
    });
  };

  handleToggle2 = (e, isToggled) => {
    this.setState({
      showZygosityOptions: isToggled
    });
  };

  render() {
    return (
      <div>
        <div style={{}}>
          <div>
            <Row>
              <Col sm={6}>
                <Toggle
                  labelStyle={{
                    fontSize: "22px",
                    fontWeight: "300",
                    color: "#333"
                  }}
                  label={
                    "ALLELE FREQUENCY " +
                    (this.state.showAlleleFrequencyOptions
                      ? Store.allelefreq + "%"
                      : "")
                  }
                  toggled={this.state.showAlleleFrequencyOptions}
                  onToggle={this.handleToggle.bind(this)}
                />

                {this.state.showAlleleFrequencyOptions && (
                  <div>
                    <Slider
                      min={0}
                      max={100}
                      step={5}
                      defaultValue={Store.allelefreq}
                      onChange={(e, value) => {
                        Store.allelefreq = value;
                      }}
                    />
                  </div>
                )}
                <br />
                <h2>CONSEQUENCE</h2>
                <TextField
                  fullWidth
                  underlineStyle={{ borderColor: "#ADADAD" }}
                  hintText="missense"
                />
              </Col>
              <Col sm={6}>
                <Toggle
                  labelStyle={{
                    fontSize: "22px",
                    fontWeight: "300",
                    color: "#333"
                  }}
                  toggled={this.state.showZygosityOptions}
                  label="METHYLATION"
                  onToggle={this.handleToggle2.bind(this)}
                />
                {this.state.showZygosityOptions && (
                  <div>
                    <RadioButtonGroup
                      name="allelefreq"
                      defaultSelected="community">
                      <RadioButton
                        style={{
                          padding: "8px"
                        }}
                        value="Hypomethylation"
                        label="Hypomethylation"
                      />
                      <RadioButton
                        style={{
                          padding: "8px"
                        }}
                        value="Hypermethylation"
                        label="Hypermethylation"
                      />
                    </RadioButtonGroup>
                  </div>
                )}
                <br />
                <h2 style={{ marginTop: "42px" }}>ANNOTATION</h2>
                <TextField
                  fullWidth
                  underlineStyle={{ borderColor: "#ADADAD" }}
                  hintText="loss of function"
                />
              </Col>
            </Row>
          </div>

          <div style={{ margin: "16px", flexGrow: "1" }} />
        </div>
      </div>
    );
  }
}
export default GeneForm;



